.playlist {
  width: calc(100% - 1rem);
  overflow: auto;
}

.playlist a.song {
  width: -moz-fit-content;
  width: fit-content;
  text-align: right;
  text-transform: uppercase;
  color: #541e3d;
  z-index: 102;
  background: linear-gradient(to top, #008cff, gold) center fixed;
  width: -moz-fit-content;
  margin: 5px 0 5px auto;
  padding: 2vmin 2vmin calc(2vmin - 2px) 0;
  display: block;
  position: relative;
}

.playlist a.song.active {
  color: #9370db;
}

.playlist a.song.active b:before {
  background: rgba(0, 0, 139, .4);
}

.playlist a.song b {
  letter-spacing: 1px;
  text-transform: lowercase;
  margin-right: 7px;
  font-size: .9rem;
  font-weight: 800;
  position: relative;
}

.playlist a.song b:before {
  content: "";
  background: rgba(255, 255, 255, .4);
  width: calc(100% + 6px);
  height: 1px;
  position: absolute;
  bottom: -3px;
  left: -3px;
}

.playlist a.song:nth-of-type(6n+1) {
  animation: 1.6s ease-in-out infinite alternate equalz;
}

.playlist a.song:nth-of-type(6n+2) {
  animation: 2s ease-in-out infinite alternate equalz;
}

.playlist a.song:nth-of-type(6n+3) {
  animation: 2.5s ease-in-out infinite alternate equalz;
}

.playlist a.song:nth-of-type(6n+4) {
  animation: 2.3s ease-in-out infinite alternate equalz;
}

.playlist a.song:nth-of-type(6n+5) {
  animation: 1.8s ease-in-out infinite alternate equalz;
}

.playlist a.song:nth-of-type(6n+6) {
  animation: 2s ease-in-out infinite alternate equalz;
}

@keyframes equalz {
  0% {
    border-left: 6vmin solid rgba(0, 0, 0, 0);
  }

  25% {
    border-left: 10vmin solid rgba(0, 0, 0, 0);
  }

  50% {
    border-left: 8vmin solid rgba(0, 0, 0, 0);
  }

  75% {
    border-left: 14vmin solid rgba(0, 0, 0, 0);
  }

  100% {
    border-left: 6vmin solid rgba(0, 0, 0, 0);
  }
}

@keyframes groove {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(5deg);
  }
}

@media (max-width: 500px) {
  * {
    animation: none !important;
  }

  .playlist a.song {
    text-align: center;
    padding: 2vmin 2vmin calc(2vmin - 2px);
    line-height: 200%;
  }
}

